/* add css module styles here (optional) */

._2dyT7 {
  width: 100%;
  border: solid 1px #ccc;
}

._2dyT7 caption {
  color:blueviolet;
  text-transform: uppercase;
}
._2dyT7 thead th {
  border-bottom: solid 1px #ccc;
  border-right: solid 1px #ccc;
  padding: 5px;
}

._2dyT7 tbody td {
  border-bottom: solid 1px #ccc;
  border-right: solid 1px #ccc;
  padding: 5px;
}

._2dyT7 thead th:last-child,
._2dyT7 tbody td:last-child {
  border-right: 0;
}
._2dyT7 tbody tr:last-child td {
  border-bottom: 0;
}
